import React from "react";
import { Link } from "react-router-dom";
import Logo from "./../../images/logo-light.png";
import Experience from "./../../images/experience.svg";
import RoomsImage from "./../../images/chat.svg";
import "./HomePage.css";

const HomePage = () => {
  return (
    <>
      {/* Section 1 */}
      <div className="hp-section1">
        <div className="hp-nav">
          <img src={Logo}></img>
        </div>
        <p>Reimagining Social Trading Experience</p>
        <p className="content">
          Stalk is a decentralized investing discussion platform.
        </p>
        <Link to="/">
          <button className="hp-btn-explore">Coming Soon</button>
        </Link>
      </div>

      {/* Section 2 */}
      <div className="hp-section2">
        <div className="hp-s2-header">
          <p>Why Stalk?</p>
        </div>
        <div className="hp-exp">
          <img src={Experience}></img>
          <div>
            <p className="hp-exp-head">TLDR: You're in control!</p>
            <div>
              <p className="content">
                Stalk is a decentralized investing discussion platform.
              </p>
              <p className="content">
                You connect to a network of peers who curate the best content
                for you. No censorship, just the power of people!
              </p>
            </div>
          </div>
        </div>
      </div>

      {/* Section 3 */}
      <div className="hp-section3">
        <div className="hp-s3-header">
          <p>Stock Talk with Traders!</p>
        </div>
        <div className="hp-exp3">
          <img src={RoomsImage}></img>
          <div>
            <br /> Once in Stalk, you can <u>s</u>tock <u>talk</u>
            &nbsp;with people who share <b>your</b> interests and want to help{" "}
            <b>you</b> be the best investor you can be. Don't like content? Just
            stop watching or unpeer that creator.
          </div>
        </div>
      </div>

      <div className="ft">
        Developed with ♥️ by <Link to="/team">PANJACS</Link>
      </div>
    </>
  );
};

export default HomePage;
